  background-color: var(--background);
  color: var(--textPrimary);
}

.v-actionsheet-header {
  padding: 12px;
  background-color: white;
  text-align: center;
}
.v-actionsheet-title {
  font-size: 17px;
  margin: 0;
  padding: 0;
  font-weight: 400;
}
.v-actionsheet-describe {
  margin: 0;
  color: var(--textRegular);
  font-size: 14px;
}

.v-actionsheet-default {
  margin: 0;
  padding: 0;
  list-style: none;
}

.v-actionsheet-item {
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  height: 48px;
  font-size: 16px;
  background-color: #fff;
  &:first-child.v-bd-y-top:before {
    display: none;
  }
  &:active {
    background-color: var(--gray);
  }
}

.v-actionsheet-item-default {
  color: var(--textPrimary);
}

.v-actionsheet-item-primary {
  color: var(--primary);
}

.v-actionsheet-item-warning {
  color: var(--error);
}

.v-actionsheet-item-disabled {
  color: var(--textSecondary);
  &:active {
    background-color: #fff;
  }
}

.v-actionsheet-align-center {
  justify-content: center;
}

.v-actionsheet-align-left {
  justify-content: left;
}

.v-actionsheet-cancel {
  margin-top: 10px;
}
</style>
